/* Grid */

item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  border: 1px solid transparent;
}

/* Grid row 1, column 1 */
item:first-child {
  grid-column: 1 / 3; /* span from grid column line 1 to 3 (i.e., span 2 columns) */
  grid-row: 1 / 3;    
}

/* Grid row 1, column 2 */
item:nth-child(2) {
  grid-column: 3 / 11;
  grid-row: 1 / 3;   
}

/* Grid row 1, column 3 */
item:nth-child(3) {
  grid-column: 11 / 13; 
  grid-row: 1 / 3;  
}

/* Grid row 1, column 9 */
item:nth-child(9) {
  grid-column: 6 / 8;
  grid-row: 2 / 4;  
}

/* Grid row 4, column 8 */
item:nth-child(44) {
  grid-column: 6 / 8; 
  grid-row: 7 / 9;  
}

